<template>
  <div>
      <div id="Logon">
      <Title title="注册" writeDisplay="none" cutDisplay="none" complainDisplay="none" finishDisplay1="none" finishDisplay2="none"/>
      <Location location="学生卡ID" choose=""/>
      <Location location="密码" choose=""/>
      <Location location="昵称" choose=""/>
      <div class="block">
          <div style="display:flex;"><p>性别</p><p style="color:red;">*</p></div>
          <form>
              <label><input type="radio" value="M" name="gender" v-model="gender" @change="change('gender')">男</label>
              <label><input type="radio" value="F" name="gender" v-model="gender" @change="change('gender')">女</label>
              <label><input type="radio" value="N" name="gender" v-model="gender" @change="change('gender')">未知</label>
          </form>
      </div>
      <div class="block" @click="test()">
          <div style="display:flex;"><p>校区</p><p style="color:red;">*</p></div>
          <form>
              <label><input type="radio" value="X" name="school" v-model="campus" @change="change('campus')"> 北洋园</label>
              <label><input type="radio" style="margin-left:20pt;" value="L" name="school" v-model="campus" @change="change('campus')">卫津路</label>
          </form>
      </div>
      <Location location="QQ" choose="(选填)"/>
      <Location location="微信" choose="(选填)"/>
      <Location location="专业" choose="(选填)"/>
  </div>
  <Change operate="注册" biggest='Logon'/>
  </div>
</template>

<script>
import Title from '../components/Public/Title.vue'
import Location from '../components/writePost/Location.vue'
import Change from '../components/Public/Change.vue'
export default {
    name:"Logon",
    components:{
        Title,
        Location,
        Change
    },
    methods:{
        change:function(name){
           if(name=="gender"){
               this.$store.state.personalInformation.gender=this.gender;
               console.log(this.$store.state.personalInformation.gender);
           }
           if(name=="campus"){
               this.$store.state.personalInformation.campus=this.campus;
           }
        },
        test:function(){
            console.log(this.$store.state.personalInformation);
            this.$store.state.personalInformation.__ob__={};
        }
    },
    data(){
        return{
            gender:"",
            campus:""
        }
    },
    mounted(){

    }
}
</script>

<style>
.block
{
    margin:5%;
    width:90%;
    display:flex;
    justify-content: space-between;
}
</style>